	}
  }
  
  .note-placeholder {
	position: absolute;
	display: none;
	color: grey;
  }
  
  .note-handle .note-control-selection {
	position: absolute;
	display: none;
	border: 1px solid #000;
  
	> div {
	  position: absolute;
	}
  
	.note-control-selection-bg {
	  width: 100%;
	  height: 100%;
	  background-color: #000;
	  -webkit-opacity: .3;
	  -khtml-opacity: .3;
	  -moz-opacity: .3;
	  opacity: .3;
	  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=30);
	  filter: alpha(opacity = 30);
	}
  
	.note-control-handle, .note-control-holder {
	  width: 7px;
	  height: 7px;
	  border: 1px solid #000;
	}
  
	.note-control-sizing {
	  width: 7px;
	  height: 7px;
	  border: 1px solid #000;
	  background-color: #000;
	}
  
	.note-control-nw {
	  top: -5px;
	  left: -5px;
	  border-right: none;
	  border-bottom: none;
	}
  
	.note-control-ne {
	  top: -5px;
	  right: -5px;
	  border-bottom: none;
	  border-left: none;
	}
  
	.note-control-sw {
	  bottom: -5px;
	  left: -5px;
	  border-top: none;
	  border-right: none;
	}
  
	.note-control-se {
	  right: -5px;
	  bottom: -5px;
	  cursor: se-resize;
  
	  &.note-control-holder {
		cursor: default;
		border-top: none;
		border-left: none;
	  }
	}
  
	.note-control-selection-info {
	  right: 0;
	  bottom: 0;
	  padding: 5px;
	  margin: 5px;
	  color: #fff;
	  background-color: #000;
	  font-size: 12px;
	  border-radius: 5px;
	  -webkit-opacity: .7;
	  -khtml-opacity: .7;
	  -moz-opacity: .7;
	  opacity: .7;
	  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=70);
	  filter: alpha(opacity = 70);
	}
  }
  
  .note-hint-popover {
	min-width: 100px;
	padding: 2px;
  
	.popover-content {
	  padding: 3px;
	  max-height: 150px;
	  overflow: auto;
  
	  .note-hint-group .note-hint-item {
		display: block !important;
		padding: 3px;
  
		&.active, &:hover {
		  display: block;
		  clear: both;
		  font-weight: 400;
		  line-height: 1.4;
		  color: #fff;
		  white-space: nowrap;
		  text-decoration: none;
		  background-color: #428bca;
		  outline: 0;
		  cursor: pointer;
		}
	  }
	}
  }
  
  .note-btn {
	&.dropdown-toggle::after {
	  display: none;
	}
  }
  
  .note-editor.note-frame.panel.panel-default .panel-heading {
	background-color: #ffffff;
	border-bottom: 1px solid #d3dfea;
  }
  
  .note-btn.btn-default {
	background-color: $background;
	border-color: #d3dfea;
  }
  
  .note-editor {
	.btn-default:not(:disabled):not(.disabled):active {
	  background-color: #f1f3f5;
	}
  
	.btn-group .btn + .btn-group {
	  border-left: 0px solid rgba(0, 0, 0, 0.08) !important;
	}
  }
  
  .panel-default.fullscreen .note-editable {
	background-color: #fff;
  }
  
  .note-btn {
	&.btn.btn-default.btn-sm {
	  line-height: 1.84615385;
	  color: #263871;
	}
  
	&.btn-default:not(:disabled):not(.disabled).active {
	  color: #495057;
	  background-color: #f1f3f5;
	  border-color: #d3d9df;
	}
  }