.morris-hover {
	position: absolute;
	z-index: 0;

	&.morris-default-style {
		border-radius: 10px;
		padding: 6px;
		color: #666;
		background: $background;
		border: solid 1px rgba(230, 230, 230, 0.1);
		font-family: sans-serif;
		font-size: 12px;
		text-align: center;

		.morris-hover-row-label {
			font-weight: bold;
			margin: 0.25em 0;
		}

		.morris-hover-point {
			white-space: nowrap;
			margin: 0.1em 0;
		}
	}
}