.lg-actions {

	.lg-next,
	.lg-prev {
		background-color: rgba(0, 0, 0, 0.45);
		border-radius: 2px;
		color: #999;
		cursor: pointer;
		display: block;
		font-size: 22px;
		margin-top: -10px;
		padding: 8px 10px 9px;
		position: absolute;
		top: 50%;
		z-index: 1080;
	}

	.lg-next.disabled,
	.lg-prev.disabled {
		pointer-events: none;
		opacity: 0.5;
	}

	.lg-next:hover,
	.lg-prev:hover {
		color: #FFF;
	}

	.lg-next {
		right: 20px;

		&:before {
			content: "\e095";
		}
	}

	.lg-prev {
		left: 20px;

		&:after {
			content: "\e094";
		}
	}
}

