.lg-toolbar {
	z-index: 1082;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
	background-color: rgba(0, 0, 0, 0.45);

	.lg-icon {
		color: #999;
		cursor: pointer;
		float: right;
		font-size: 24px;
		height: 47px;
		line-height: 27px;
		padding: 10px 0;
		text-align: center;
		width: 50px;
		text-decoration: none !important;
		outline: medium none;
		-webkit-transition: color 0.2s linear;
		-o-transition: color 0.2s linear;
		transition: color 0.2s linear;

		&:hover {
			color: #FFF;
		}
	}

	.lg-close:after {
		content: "\e070";
	}

	.lg-download:after {
		content: "\e0f2";
	}
}

.lg-sub-html {
	background-color: rgba(0, 0, 0, 0.45);
	bottom: 0;
	color: #EEE;
	font-size: 16px;
	left: 0;
	padding: 10px 40px;
	position: fixed;
	right: 0;
	text-align: center;
	z-index: 1080;

