			-o-transition: bottom 0.25s ease;
			transition: bottom 0.25s ease;
		}

		&.lg-thumb-open .lg-sub-html {
			bottom: 100px;
		}
	}

	.lg-toggle-thumb {
		background-color: #0D0A0A;
		border-radius: 2px 2px 0 0;
		color: #999;
		cursor: pointer;
		font-size: 24px;
		height: 39px;
		line-height: 27px;
		padding: 5px 0;
