			visibility: visible;
		}

		#lg-share {
			color: #FFF;
		}
	}

	.lg-dropdown {
		background-color: #fff;
		border-radius: 2px;
		font-size: 14px;
		list-style-type: none;
		margin: 0;
		padding: 10px 0;
		position: absolute;
		right: 0;
		text-align: left;
		top: 50px;
		opacity: 0;
		visibility: hidden;
		-moz-transform: translate3d(0, 5px, 0);
		-o-transform: translate3d(0, 5px, 0);
		-ms-transform: translate3d(0, 5px, 0);
		-webkit-transform: translate3d(0, 5px, 0);
		transform: translate3d(0, 5px, 0);
		-webkit-transition: -webkit-transform 0.18s linear 0s, visibility 0s linear 0.5s, opacity 0.18s linear 0s;
		-moz-transition: -moz-transform 0.18s linear 0s, visibility 0s linear 0.5s, opacity 0.18s linear 0s;
		-o-transition: -o-transform 0.18s linear 0s, visibility 0s linear 0.5s, opacity 0.18s linear 0s;
		transition: transform 0.18s linear 0s, visibility 0s linear 0.5s, opacity 0.18s linear 0s;

		&:after {
			content: "";
			display: block;
			height: 0;
			width: 0;
			position: absolute;
			border: 8px solid transparent;
			border-bottom-color: #FFF;
			right: 16px;
			top: -16px;
		}

		>li {
			&:last-child {
				margin-bottom: 0px;
			}

			&:hover {

				a,
				.lg-icon {
					color: #333;
				}
			}
		}

		a {
			color: #333;
			display: block;
			white-space: pre;
			padding: 4px 12px;
			font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
			font-size: 12px;

			&:hover {
				background-color: rgba(0, 0, 0, 0.07);
			}
		}

		.lg-dropdown-text {
			display: inline-block;
			line-height: 1;
			margin-top: -3px;
			vertical-align: middle;
		}

		.lg-icon {
			color: #333;
			display: inline-block;
			float: none;
			font-size: 20px;
			height: auto;
			line-height: 1;
			margin-right: 8px;
			padding: 0;
			vertical-align: middle;
			width: auto;
		}
	}

	#lg-share {
		position: relative;

		&:after {
			content: "\e80d";
		}
	}

	#lg-share-facebook .lg-icon {
		color: #3b5998;

		&:after {
			content: "\e901";
		}
	}

	#lg-share-twitter .lg-icon {
		color: #00aced;

		&:after {
			content: "\e904";
		}
	}

	#lg-share-googleplus .lg-icon {
		color: #dd4b39;

		&:after {
			content: "\e902";
		}
	}

	#lg-share-pinterest .lg-icon {
		color: #cb2027;

		&:after {
			content: "\e903";
		}
	}
}

.group {
	*zoom: 1;

	&:before {
		display: table;
		content: "";
		line-height: 0;
	}

	&:after {
		display: table;
		content: "";
		line-height: 0;
		clear: both;
	}
}

.lg-outer {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1050;
	opacity: 0;
	-webkit-transition: opacity 0.15s ease 0s;
	-o-transition: opacity 0.15s ease 0s;
	transition: opacity 0.15s ease 0s;

	* {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}

	&.lg-visible {
		opacity: 1;
	}

	&.lg-css3 {
		.lg-item {

			&.lg-prev-slide,
			&.lg-next-slide,
			&.lg-current {
				-webkit-transition-duration: inherit !important;
				transition-duration: inherit !important;
				-webkit-transition-timing-function: inherit !important;
				transition-timing-function: inherit !important;
			}
		}

		&.lg-dragging .lg-item {

			&.lg-prev-slide,
			&.lg-next-slide,
			&.lg-current {
				-webkit-transition-duration: 0s !important;
				transition-duration: 0s !important;
				opacity: 1;
			}
		}
	}

	&.lg-grab img.lg-object {
		cursor: -webkit-grab;
		cursor: -moz-grab;
		cursor: -o-grab;
		cursor: -ms-grab;
		cursor: grab;
	}

	&.lg-grabbing img.lg-object {
		cursor: move;
		cursor: -webkit-grabbing;
		cursor: -moz-grabbing;
		cursor: -o-grabbing;
		cursor: -ms-grabbing;
		cursor: grabbing;
	}

	.lg {
		height: 100%;
		width: 100%;
		position: relative;
		overflow: hidden;
		margin-left: auto;
		margin-right: auto;
		max-width: 100%;
		max-height: 100%;
	}

	.lg-inner {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		white-space: nowrap;
	}

	.lg-item {
		// background: url("../img/loading.gif") no-repeat scroll center center transparent;
		display: none !important;
	}

	&.lg-css3 {

		.lg-prev-slide,
		.lg-current,
		.lg-next-slide {
			display: inline-block !important;
		}
	}

	&.lg-css .lg-current {
		display: inline-block !important;
	}

	.lg-item,
	.lg-img-wrap {
		display: inline-block;
		text-align: center;
		position: absolute;
		width: 100%;
		height: 100%;
	}

	.lg-item:before {
		content: "";
		display: inline-block;
		height: 50%;
		width: 1px;
		margin-right: -1px;
	}

	.lg-img-wrap {
		&:before {
			content: "";
			display: inline-block;
			height: 50%;
			width: 1px;
			margin-right: -1px;
		}

		position: absolute;
		padding: 0 5px;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
	}

	.lg-item {
		&.lg-complete {
			background-image: none;
		}

		&.lg-current {
			z-index: 1060;
		}
	}

	.lg-image {
		display: inline-block;
		vertical-align: middle;
		max-width: 100%;
		max-height: 100%;
		width: auto !important;
		height: auto !important;
	}

	&.lg-show-after-load .lg-item {

		.lg-object,
		.lg-video-play {
			opacity: 0;
			-webkit-transition: opacity 0.15s ease 0s;
			-o-transition: opacity 0.15s ease 0s;
			transition: opacity 0.15s ease 0s;
		}

		&.lg-complete {

			.lg-object,
			.lg-video-play {
				opacity: 1;
			}
		}
	}

	.lg-empty-html,
	&.lg-hide-download #lg-download {
		display: none;
	}
}

.lg-backdrop {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 1040;
	background-color: #000;
	opacity: 0;
	-webkit-transition: opacity 0.15s ease 0s;
	-o-transition: opacity 0.15s ease 0s;
	transition: opacity 0.15s ease 0s;

	&.in {
		opacity: 1;
	}
}

.lg-css3 {
	&.lg-no-trans {

		.lg-prev-slide,
		.lg-next-slide,
		.lg-current {
			-webkit-transition: none 0s ease 0s !important;
			-moz-transition: none 0s ease 0s !important;
			-o-transition: none 0s ease 0s !important;
			transition: none 0s ease 0s !important;
		}
	}

	&.lg-use-css3 .lg-item,
	&.lg-use-left .lg-item {
		-webkit-backface-visibility: hidden;
		-moz-backface-visibility: hidden;
		backface-visibility: hidden;
	}

	&.lg-fade .lg-item {
		opacity: 0;

		&.lg-current {
			opacity: 1;
		}

		&.lg-prev-slide,
		&.lg-next-slide,
		&.lg-current {
			-webkit-transition: opacity 0.1s ease 0s;
			-moz-transition: opacity 0.1s ease 0s;
			-o-transition: opacity 0.1s ease 0s;
			transition: opacity 0.1s ease 0s;
		}
	}

	&.lg-slide {
		&.lg-use-css3 .lg-item {
			opacity: 0;

			&.lg-prev-slide {
				-webkit-transform: translate3d(-100%, 0, 0);
				transform: translate3d(-100%, 0, 0);
			}

			&.lg-next-slide {
				-webkit-transform: translate3d(100%, 0, 0);
				transform: translate3d(100%, 0, 0);
			}

			&.lg-current {
				-webkit-transform: translate3d(0, 0, 0);
				transform: translate3d(0, 0, 0);
				opacity: 1;
			}

			&.lg-prev-slide,
			&.lg-next-slide,
			&.lg-current {
				-webkit-transition: -webkit-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
				-moz-transition: -moz-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
				-o-transition: -o-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
				transition: transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
			}
		}

		&.lg-use-left .lg-item {
			opacity: 0;
			position: absolute;
			left: 0;

			&.lg-prev-slide {
				left: -100%;
			}

			&.lg-next-slide {
				left: 100%;
			}

			&.lg-current {
				left: 0;
				opacity: 1;
			}

			&.lg-prev-slide,
			&.lg-next-slide,
			&.lg-current {
				-webkit-transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
				-moz-transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
				-o-transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
				transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
			}
		}
	}
}