.ui-timepicker-wrapper {
	overflow-y: auto;
	max-height: 150px;
	width: 6.5em;
	background: #fff;
	border: 1px solid #ddd;
	-webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
	-moz-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
	outline: none;
	z-index: 10001;
	margin: 0;

	&.ui-timepicker-with-duration {
		width: 13em;

		&.ui-timepicker-step-30,
		&.ui-timepicker-step-60 {
			width: 11em;
		}
	}
}

.ui-timepicker-list {
	margin: 0;
	padding: 0;
	list-style: none;
}

.ui-timepicker-duration {
	margin-left: 5px;
	color: #888;
}

.ui-timepicker-list {
	&:hover .ui-timepicker-duration {
		color: #888;
	}

	li {
		padding: 3px 0 3px 5px;
		cursor: pointer;
		white-space: nowrap;
		color: #000;
		list-style: none;
		margin: 0;
	}

	&:hover .ui-timepicker-selected {
		background: #fff;
		color: #000;
	}
}

li.ui-timepicker-selected {
	background: $primary;
	color: #fff;
}

.ui-timepicker-list {

	li:hover,
	.ui-timepicker-selected:hover {
		background: $primary;
		color: #fff;
	}
}

li.ui-timepicker-selected .ui-timepicker-duration {
	color: #ccc;
}

.ui-timepicker-list li {
	&:hover .ui-timepicker-duration {
		color: #ccc;
	}

	&.ui-timepicker-disabled {
		color: #888;
		cursor: default;

		&:hover {
			color: #888;
			cursor: default;
		}
	}

	&.ui-timepicker-selected.ui-timepicker-disabled {
		color: #888;
		cursor: default;
	}

	&.ui-timepicker-disabled:hover,
	&.ui-timepicker-selected.ui-timepicker-disabled {
		background: #f2f2f2;
	}
}